.blog-detail {
    display: flex;
    flex-direction: column;

    &-title {
        font-size: 3rem;
        font-weight: 600;
        color: $dark;
        margin-bottom: 70px;
    }

    &-img {
        width: 100%;
        max-width: 100%;
        height: 258px;
        border-radius: 15px;
        margin-bottom: 60px;
        object-fit: cover;
    }

    p {
        font-size: 2rem;
        font-weight: 400;
        color: $dark;
        line-height: 40px;
    }
}