// Profile title
.profile-title {
  color: $dark;
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 35px;
}

// Profile
.profile {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  .profile-preview {
    display: flex;
    flex-direction: column;
    margin-right: 80px;

    @media only screen and (max-width: 1262px) {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    }

    @media only screen and (max-width: 540px){
      @include centerFlex;
      flex-basis: 540px;
        max-width: 540px;
      
    }


    .user-photo {
      padding: 20px;
      background-color: $light;
      width: 290px;
      max-width: 290px;
      border-radius: 15px;
      margin-bottom: 85px;

      @media only screen and (max-width: 1262px) {
        margin-right: 20px;
      }

      @media only screen and (max-width: 540px) {
        flex-basis: 540px;
        max-width: 540px;
        margin-left: 40px;
      }

      img {
        width: 100%;
        height: 250px;
        object-fit: cover;
        border-radius: 15px;
        margin-bottom: 20px;
      }

      .upload-photo {
        .text {
          @include centerFlex;
          height: 40px;
          border-radius: 15px;
          cursor: pointer;
          background-color: $light;
          color: $dark;
          border: 2px solid $grey;
          font-size: 1rem;
          font-weight: 400;
          transition: 250ms all;

          &:hover {
            background-color: $primary;
            color: $light;
            border: 2px solid $grey-dark;
          }

          i {
            margin-right: 7px;
          }
        }
        input {
          display: none;
        }
      }
    }

    .social-media {
      display: flex;
      flex-direction: column;

      @media only screen and (max-width: 1262px) {
        display: flex;        
      }

      .title {
        color: $grey;
        font-size: 0.9rem;
        font-weight: 400;
        margin-bottom: 10px;
      }

      .social--card {
        display: flex;
        align-items: center;
        background-color: $light;
        border-radius: 15px;
        color: $dark;
        padding: 10px;
        &:not(:last-child) {
          margin-bottom: 20px;
        }



        .social--logo {
          @include centerFlex;
          width: 40px;
          height: 40px;
          color: $light;
          border-radius: 12px;
          margin-right: 10px;

          &.facebook {
            background-color: #4267b2;
          }
          &.twitter {
            background-color: #00acee;
          }
          &.instagram {
            background: #f09433;
            background: -moz-linear-gradient(
              45deg,
              #f09433 0%,
              #e6683c 25%,
              #dc2743 50%,
              #cc2366 75%,
              #bc1888 100%
            );
            background: -webkit-linear-gradient(
              45deg,
              #f09433 0%,
              #e6683c 25%,
              #dc2743 50%,
              #cc2366 75%,
              #bc1888 100%
            );
            background: linear-gradient(
              45deg,
              #f09433 0%,
              #e6683c 25%,
              #dc2743 50%,
              #cc2366 75%,
              #bc1888 100%
            );
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 );
          }
          &.telegram {
            background-color: #0088cc;
          }
        }

        .social--company-title {
          font-size: 1rem;
          font-weight: 400;
          color: $dark;
          margin-right: auto;
        }

        .social--action {
          display: flex;

          .icon--done {
            @include centerFlex;
            width: 26px;
            height: 26px;
            font-size: 0.8rem;
            background-color: $success;
            color: $light;
            margin-right: 10px;
            border-radius: 50%;
          }

          .icon--plus {
            @include centerFlex;
            width: 26px;
            height: 26px;
            font-size: 0.8rem;
            background-color: $primary;
            color: $light;
            margin-right: 10px;
            border-radius: 50%;
          }

          .icon--icon {
            @include centerFlex;
            width: 26px;
            height: 26px;
            font-size: 0.8rem;
            color: $grey;
            margin-right: 10px;
            border-radius: 50%;
          }
        }
      }
    }
  }

  .profile-setting {
    display: flex;
    flex-direction: column;
    margin-right: 80px;
    flex-basis: 490px;
    max-width: 490px;

    @media only screen and (max-width: 1262px) {
      display: flex;
      flex-direction: column;
      margin-right: 80px;
      flex-basis: 100%;
      max-width: 100%;
      margin-top: 30px;
    }

    .user-setting-form {
      display: flex;
      flex-direction: column;

      label {
        color: $grey;
        font-size: 1rem;
        font-weight: 400;
        margin-bottom: 10px;
      }

      input {
        background-color: $grey-dark;
        color: $dark;
        padding: 15px 20px;
        border-radius: 15px;
        font-size: 1rem;
        font-weight: 400;
        margin-bottom: 20px;
      }

      .user-info {
        display: flex;
        flex-direction: column;

        .title {
          font-size: 1.3rem;
          color: $grey;
          font-weight: 400;
          margin-bottom: 18px;
        }
      }
    }
  }

  .profile-action {
      display: flex;
      flex-direction: column;
      
      @media only screen and (max-width: 1262px) {
        width: 100%;
        margin-top: 30px;
      }

    &-title {
      font-size: 0.9rem;
      font-weight: 400;
      color: $grey;
      margin-bottom: 10px;
    }

    .static-card {
      display: flex;
      align-items: center;
      height: 90px;
      background-color: $light;
      color: $dark;
      margin-bottom: 20px;
      padding: 20px;
      border-radius: 15px;

      .static-icon {
        width: 50px;
        height: 50px;
        border-radius: 12px;
        @include centerFlex;
        margin-right: 20px;

        &.heart {
          background-color: #ffcece;
          color: #e24444;
        }

        &.plus {
          background-color: #f1deff;
          color: #a86ce4;
        }

        &.calendar {
          background-color: #deffe5;
          color: #50d86a;
        }

        &.bitcoin {
          background-color: #fff6de;
          color: #ffa10b;
        }
      }

      .static-info {
        display: flex;
        flex-direction: column;

        .num {
          font-size: 1.4rem;
          font-weight: 400;
          color: $dark;
          margin-bottom: 5px;
        }

        .discription {
          font-size: 1rem;
          font-weight: 400;
          color: $grey;
        }
      }
    }
  }

}

