// Menu Title
.menu-title {
    color: $dark;
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 35px;
}

.menu {
    display: flex;
    flex-direction: column;

    ul {

        li {
            .logout {
                color: $danger;
              }
            margin-bottom: 30px;

            a {
                color: $dark;
                font-size: 1rem;
                font-weight: 400;

            }

            i {
                margin-right: 10px;
            }
        }
    }

    .copyright {
        display: none;

        @media only screen and (max-width: 540px) {
            @include centerFlex;
            margin-top: 60px;
            margin-bottom: 60px;
            color: $dark;
            font-size: 0.8rem;
            font-weight: 400;
        }
    }

}